import { Suspense, useMemo, useEffect, useState } from "react";
import * as THREE from 'three';
import { useThree, useFrame } from "@react-three/fiber";
import CameraControls from 'camera-controls';

CameraControls.install({ THREE })

export function SceneContainer(){
  const [zoom, setZoom] = useState(false)
  const [focus, setFocus] = useState({})
  const [zoom1, setZoom1] = useState(false)
  const [focus1, setFocus1] = useState({})

 return (
    <Suspense fallback={null}>
      <Lights />
      <Screen zoomToView1={(focusRef1) => (setZoom1(!zoom1), setFocus1(focusRef1))} />
      <Screen1 zoomToView1={(focusRef1) => (setZoom1(!zoom1), setFocus1(focusRef1))} />

      <Controls zoom={zoom} focus={focus} />
      <Controls1 zoom1={zoom1} focus1={focus1} />
    </Suspense>
 ); 
}

function Controls({ zoom, focus, pos = new THREE.Vector3(), look = new THREE.Vector3() }) {
  const camera = useThree((state) => state.camera)
  const gl = useThree((state) => state.gl)
  const controls = useMemo(() => new CameraControls(camera, gl.domElement), [])

  return useFrame((state, delta) => {
    zoom ? pos.set(focus.x * Math.PI, focus.y, focus.z) : pos.set(5, 5, 5)
    zoom ? look.set(focus.x, focus.y, focus.z) : look.set(4, 4, 4)

    state.camera.position.lerp(pos, 0.5)
    state.camera.updateProjectionMatrix()

    controls.setLookAt(state.camera.position.x, state.camera.position.y, state.camera.position.z, look.x, look.y, look.z, true)
    return controls.update(delta)
  })
}

function Controls1({ zoom1, focus1, pos1 = new THREE.Vector3(), look1 = new THREE.Vector3() }) {
  const camera = useThree((state) => state.camera)
  const gl = useThree((state) => state.gl)
  const controls = useMemo(() => new CameraControls(camera, gl.domElement), [])

  return useFrame((state, delta) => {
    zoom1 ? pos1.set(focus1.x - 1.8, focus1.y - 1, focus1.z - 2) : pos1.set(5, 5, 5)
    zoom1 ? look1.set(focus1.x, focus1.y, focus1.z) : look1.set(4, 4, 4)

    state.camera.position.lerp(pos1, 0.5)
    state.camera.updateProjectionMatrix()

    controls.setLookAt(state.camera.position.x, state.camera.position.y, state.camera.position.z, look1.x, look1.y, look1.z, true)
    return controls.update(delta)
  })
}

function Screen({ zoomToView1 }) {
  const [video] = useState(() =>Object.assign(document.createElement('video'), { src: '/videos/1.mp4', loop: false, muted: true }))
  useEffect(() => void video.play(), [video])

  return (
    <mesh scale-x={1.4} scale-y={0.6} rotation-y={(Math.PI * 0.5)} position={[-1.42, 1.9, 1.27]} onClick={(e) => zoomToView1(e.object.position)}>
      <planeBufferGeometry />
      <meshBasicMaterial toneMapped={false}>
        <videoTexture attach="map" args={[video]} encoding={THREE.sRGBEncoding} />
      </meshBasicMaterial>
    </mesh>
  )
}

function Screen1({ zoomToView1 }) {
  const [video] = useState(() =>Object.assign(document.createElement('video'), { src: '/videos/2.mp4', loop: true, muted: true }))
  useEffect(() => void video.play(), [video])

  return (
    <mesh scale-x={0.47} scale-y={0.7} rotation-x={(-Math.PI * 0.5)} rotation-z={(Math.PI * 0)} position={[1.279, 1.21, -0.26]} onClick={(e) => zoomToView1(e.object.position)}>
      <planeBufferGeometry />
      <meshBasicMaterial toneMapped={false}>
        <videoTexture attach="map" args={[video]} encoding={THREE.sRGBEncoding} />
      </meshBasicMaterial>
    </mesh>
  )
}

function Lights() {
  return <spotLight intensity={5} position={[-4.4, 3, -0.6]} angle={0.33} color="#FF2929" castShadow />
}

