const validation = (values) => {
  let errors={};
  
  if(!values.nameFrom){
    errors.nameFrom="Name is required"
  }
  if(!values.emailFrom){
    errors.emailFrom="Email is required"
  } else if(!/\S+@\S+\.\S+/.test(values.emailFrom)){
    errors.emailFrom="Email is invalid"
  }
  if(!values.message){
    errors.message="Message is required"
  }

  return errors;
}
export default validation