import React, { useEffect, useState, useRef  } from 'react'
import "./Modal.css"
import "./tailwind.min.css"
import validation from './validation'
import emailjs from '@emailjs/browser'

const Form = ({submitForm}) => {
  const [values, setValues] = useState({
    nameFrom: "",
    emailFrom: "",
    message: "",
  })

  const [errors, setErrors] = useState({})

  const handleChange = (e) => {
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    })
  }

  const [dataIsCorrect, setDataIsCorrect] = useState(false);

  const form = useRef();

  const handleForSubmit = (e) => {
    e.preventDefault();
    setErrors(validation(values));
    setDataIsCorrect(true);
  }

  useEffect(() => {
    if(Object.keys(errors).length === 0 && dataIsCorrect) {
      submitForm(true);
      emailjs.sendForm(
        process.env.REACT_APP_SERVICE_ID, 
        process.env.REACT_APP_TEMPLATE_ID, 
        form.current, 
        process.env.REACT_APP_PUBLIC_KEY
      );
    }
  }, [errors]);

  return (
    <form ref={form} className="contact_form">
      <div className="mb-2 pt-0">
        <label htmlFor="nameFrom">Name {errors.nameFrom && <p className="error">{errors.nameFrom}</p>}</label>
        <input type="text" placeholder="Your name" name="nameFrom" id="nameFrom" value={values.nameFrom} onChange={handleChange} className="px-3 py-3 placeholder-gray-400 text-gray-600 relative bg-white bg-white rounded text-sm border-2 shadow outline-none focus:outline-none focus:ring w-full" required />
      </div>
      <div className="mb-2 pt-0">
        <label htmlFor="emailFrom">Email {errors.emailFrom && <p className="error">{errors.emailFrom}</p>}</label>
        <input type="email" placeholder="Email" name="emailFrom" id="emailFrom" value={values.emailFrom} onChange={handleChange} className="px-3 py-3 placeholder-gray-400 text-gray-600 relative bg-white bg-white rounded text-sm border-2 shadow outline-none focus:outline-none focus:ring w-full" required />
      </div>
      <div className="mb-2 pt-0">
      <label htmlFor="message">Message {errors.message && <p className="error">{errors.message}</p>}</label>
        <textarea placeholder="Your message" name="message" id="message" value={values.message} onChange={handleChange} style={{minHeight: '100%', maxHeight: 50}} className="px-3 py-3 placeholder-gray-400 text-gray-600 relative bg-white bg-white rounded text-sm border-2 shadow outline-none focus:outline-none focus:ring w-full" required />
      </div>
      <div className="footer">
        <button type="submit" onClick={handleForSubmit}>Send</button>
      </div>
    </form>
  )
}

export default Form